.item-news {
  background-color: #fff;
  position: relative;
}

.item-news__title h3 {
  margin: 0;
}

.item-news__text p {
  line-height: 22px;
}

.item-news__text p:last-child {
  margin-bottom: 0;
}

.item-news__image img {
  height: 100%;
  object-fit: cover;
  width: 100%;
}

.item-news__content {
  position: initial;
}

.item-news__date {
  font-weight: 700;
  color: #fff;
  line-height: 15px;
  font-size: 15px;
  padding: 10px 20px;
  background-color: #616161;
  max-width: max-content;
}

@media only screen and (min-width: 768px) {
  .item-news {
    display: grid;
    grid-template: 1fr/1fr 2fr;
    margin: 40px 0;
    grid-gap: 25px;
  }

  .item-news__text p {
    margin: 10px 0;
  }

  .item-news__content {
    padding: 20px 0;
  }

  .item-news__date {
    margin: 10px 0;
  }
}

@media only screen and (min-width: 1024px) {
  .item-news__content {
    padding: 20px 0;
  }

  .item-news__date {
    margin: 20px 0;
  }
}

@media only screen and (max-width: 767px) {
  .item-news {
    padding: 30px 18px;
  }

  .item-news__title h3 {
    margin: 20px 0 15px;
  }

  .item-news__date {
    position: absolute;
    top: 30px;
  }
}